.blog-details-social {
  margin: 20px 0;
  grid-column: 1;
  grid-row: 1 / 3;
  padding: 0 1rem;
  @include minMedium {
    margin-top: 100px;
  }
}

.latest_blogs__container {
  display: none;

  @include minLarge {
    margin-top: 100px;
    display: block;
  }
}

.footer_triangle {
  margin-top: 100px;

  @include minLarge {
    display: none;
  }
}
